import React, { Component } from "react";
import { PricingCard } from "./Parts/PricingCard/PricingCard";
import { Link } from "react-router-dom";

export default class GlobalMainSection extends Component {
  state = { Styles: { height: "auto" }, Data: "700.953" };

  render() {
    const { Styles } = this.state;
    const onTabSwitch = () => {};
    const handleTabSwtich = () => {};
    return (
      <>
        <div
          className="card text-center list-group list-group-item list-group-flush"
          style={Styles}
        >
          <div className=" card-header list-group-item ">
            <ul
              className="nav nav-tabs card-header-tabs"
              id="myTab"
              role="tablist"
            >
              <li className="nav-item" role="presentation">
                <Link
                  className="nav-link active"
                  aria-current="true"
                  id="home-tab"
                  data-bs-toggle="tab"
                  data-bs-target="#home-tab-pane"
                  type="button"
                  role="tab"
                  aria-controls="home-tab-pane"
                  aria-selected="true"
                >
                  Web-Design
                </Link>
              </li>
              <li className="nav-item" role="presentation">
                <Link
                  className="nav-link"
                  id="contact-tab"
                  data-bs-toggle="tab"
                  data-bs-target="#contact-tab-pane"
                  type="button"
                  role="tab"
                  aria-controls="contact-tab-pane"
                  aria-selected="false"
                >
                  Discord
                </Link>
              </li>
              <li className="nav-item" role="presentation">
                <Link
                  className="nav-link disabled"
                  id="disabled-tab"
                  data-bs-toggle="tab"
                  data-bs-target="#disabled-tab-pane"
                  type="button"
                  role="tab"
                  aria-controls="disabled-tab-pane"
                  aria-selected="false"
                  disabled
                >
                  Disabled
                </Link>
              </li>
            </ul>
          </div>
          <div className="card-body list-group list-group-item list-group-flush">
            <h1 className="text-center mb">Our Pricing Packages</h1>
            <div className="row row-cols-1 row-cols-md-4">
              <div className="col">
                <PricingCard
                  className=" tab-pane fade show active"
                  id="home-tab-pane"
                  role="tabpanel"
                  aria-labelledby="home-tab"
                  tabindex="0"
                  Title={`Basic`}
                  Price={`149.99`}
                  Description={`For small businesses or individuals looking for a simple online presence.`}
                  Ft1={`A small number of web pages (e.g. home page, about us, contact us)`}
                  Ft2={`A basic design template`}
                  Ft3={`Basic on-page SEO`}
                  LinkTitle={`Sign Up`}
                  LinkPath={`signup`}
                />
              </div>
              <div className="col">
                <PricingCard
                  className="tab-pane fade"
                  id="profile-tab-pane"
                  role="tabpanel"
                  aria-labelledby="profile-tab"
                  tabindex="0"
                  Title={`Standard`}
                  Price={`299.99`}
                  Description={`For small to medium-sized businesses looking for a more robust online presence.`}
                  Ft1={`Customized design and branding elements`}
                  Ft2={`Advanced SEO and analytics tracking`}
                  Ft3={`E-commerce integration and payment gateway setup`}
                  LinkTitle={`Sign Up`}
                  LinkPath={`signup`}
                />
              </div>
              <div className="col">
                <PricingCard
                  Title={`Premium`}
                  Price={`499.99`}
                  Description={`For larger businesses or organizations looking for a highly customized and sophisticated website.`}
                  Ft1={`Feature 1`}
                  Ft2={`Feature 2`}
                  Ft3={`Feature 3`}
                  LinkTitle={`Sign Up`}
                  LinkPath={`signup`}
                />
              </div>
              <div className="col">
                <PricingCard
                  Title={`Maintence`}
                  Price={`149.99`}
                  Description={`For larger businesses or organizations looking to ensure their website stays up-to-date and secure.`}
                  Ft1={`Website updates and maintenance`}
                  Ft2={`Monthly backups and security checks`}
                  Ft3={`Priority support`}
                  LinkTitle={`Sign Up`}
                  LinkPath={`signup`}
                />
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}
