import React, { Component } from "react";

import { Link } from "react-router-dom";

export default class GlobalMainSection extends Component {
  state = {};
  render() {
    return (
      <div id="Tablet_HomePageMainSection">
        <div className="Tablet_Introduction">
          <div className="Tablet_HomePageIntro">
            <p>
              Here at BorDev Tech we provide top-notch service to get you on
              your feet and ready to go!
            </p>
            <p>
              Our wide range of experience helps us, help you in building
              anything you can dream of.
            </p>
          </div>
          <div className="Tablet_HomePageIntroButtonSection">
            <button className="Tablet_HomePageIntroButtons">
              <Link to="/signup" className="Tablet_HomePageIntroButtonLink">
                Get Started
              </Link>
            </button>
            <hr className="HomePageIntroButtonsDivider" />
            <button className="Tablet_HomePageIntroButtons">
              <Link to="/pricing" className="Tablet_HomePageIntroButtonLink">
                Pricing
              </Link>
            </button>
          </div>
        </div>
      </div>
    );
  }
}
