// REQ Imports
import React, { Component } from "react";
// Section Cards
import "bootstrap/dist/css/bootstrap.css";

export default class GlobalMainSection extends Component {
  state = {
    Data: {
      ViewMode: {
        Global: {
          Size: "Global_",
          ViewportSize: {},
          Style: {},
        },
      },
      links: ["dream", "team", "plan", "work", "tools", "results"],
      names: [
        "The Dream",
        "The Team",
        "The Plan",
        "The Work",
        "The Tools",
        "The Results",
      ],
    },
  };

  render() {
    const { links, names, ViewMode } = this.state.Data;
    const linkList = links;
    const nameList = names;

    const { Size, ViewportSize } = ViewMode.Global;
    return (
      <div id={`${Size}AboutPageMainSection`}>
        <h1 className={`${Size}AboutPageMainSectionSize`}>About Us</h1>

        <div style={{ display: "flex", flexDirection: "column" }}>
          <div style={{ display: "flex", flexDirection: "row" }}>
            {linkList.map((link, index) => (
              <button key={link} id={index} className="btn btn-primary">
                <p>{nameList[index]}</p>
              </button>
            ))}
          </div>
        </div>
      </div>
    );
  }
}
