// REQ Imports
import React, { Component } from "react";

import { Link, Outlet } from "react-router-dom";
// Section Cards

export default class TabletMainSection extends Component {
  state = {};
  render() {
    return (
      <div id="Tablet_AboutPageMainSection" style={{ maxWidth: "768px" }}>
        {/* Page Title */}
        <h1 className="Tablet_AboutPageSectionTitle">About Us</h1>
        {/* Page Body */}
        <div className="Tablet_Introduction">
          <ul className="Tablet_AboutPageSectionOptionsList">
            <button className="Tablet_AboutPageSectionOptionsListOption 0">
              <p>The Test</p>
            </button>
            <button className="Tablet_AboutPageSectionOptionsListOption 1">
              <Link to="dream">The Dream</Link>
            </button>
            <button className="Tablet_AboutPageSectionOptionsListOption 2">
              <Link to="team">
                <p>The Team</p>
              </Link>
            </button>
            <button className="Tablet_AboutPageSectionOptionsListOption 3">
              <Link to="plan">
                <p>The Plan</p>
              </Link>
            </button>
            <button className="Tablet_AboutPageSectionOptionsListOption 4">
              <Link to="work">
                <p>The Work</p>
              </Link>
            </button>
            <button className="Tablet_AboutPageSectionOptionsListOption 5">
              <Link to="tools">
                <p>The Tools</p>
              </Link>
            </button>
            <button className="Tablet_AboutPageSectionOptionsListOption 6">
              <Link to="results">
                <p>The Results</p>
              </Link>
            </button>
            <Outlet />
          </ul>
        </div>
      </div>
    );
  }
}
