import React, { Component } from 'react';


class PageRoutes extends Component {
    state = {
        Data: {
            Pages: ["a", "b", "c"],
            RouteLinks: ["alpha", "bravo", "charlie"]
        }
    }
    render() {
        const { Pages } = this.state.Data;
        return (
            <>
                {Pages.map(({ index }) => {
                    <option key={index} value={index} />
                })}
            </>
        )
    }
    ;
}

export default PageRoutes;